.Projectcard-img-top {
  width: 100%;
  height: 10vw;
  object-fit: contain;
}

@media (max-width: 500px) {
  .Projecttitle {
    font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol";
    color: #696969;
    font-weight: 300;
    font-style: inherit;
    text-align: center;
    font-size: 1.5rem;
    padding-top: 5%;
    padding-bottom: 5%;
  }
}

@media (min-width: 501px) {
  .Projecttitle {
    font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol";
    color: #696969;
    font-weight: 300;
    font-style: inherit;
    text-align: center;
    font-size: 1.5rem;
    padding-top: 5%;
    padding-bottom: 5%;
  }
}
