@media (max-width: 500px) {
  .title {
    font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol";
    color: #696969;
    font-weight: 300;
    font-style: inherit;
    text-align: center;
    font-size: 1.5rem;
    padding-top: 5%;
    padding-bottom: 5%;
  }

  .TechnicalStrengthsCard-img-top {
    width: 100%;
    height: 10vw;
    object-fit: contain;
  }
}

@media (min-width: 501px) {
  .title {
    font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol";
    color: #696969;
    font-weight: 300;
    font-style: inherit;
    text-align: center;
    font-size: 1.5rem;
    padding-top: 5%;
    padding-bottom: 5%;
  }

  .TechnicalStrengthsCard-img-top {
    width: 100%;
    height: 3vw;
    object-fit: contain;
  }
}

.card-group {
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 1vw;
}

.language {
  object-fit: contain;
  max-width: 50%;
  max-height: 50%;
}

.bg-light {
  background-color: black;
}
