.EndBanner p {
  color: black;
  position: relative;
  top: 50%;
  transform: translateY(50%);
}

.EndBanner {
  background-color: white;
  min-height: auto;
}

.EndBanner h1 {
  font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  color: #696969;
  font-weight: 300;
  font-style: inherit;
  text-align: center;
  font-size: 1.5rem;
  padding-top: 5%;
  padding-bottom: 5%;
}

.ContactBtn {
  background-color: white;
  border: 0;
  margin-right: 10%;
  margin-left: 10%;
  display: inline-block;
}

.ContactBtnContainer {
  padding-left: 20%;
  padding-right: 20%;
  padding-bottom: 100px;
}

/* 2D TRANSITIONS */
/* Grow */

.ContactBtn:hover {
  -webkit-transform: scale(1.4);
  transform: scale(1.4);
}
