.body {
  font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  color: black;
  font-style: inherit;
  padding-left: 10%;
  padding-right: 10%;
  text-align: center;
  /* padding-top: 5%; */
}

@media screen and (max-width: 500px) {
  .body {
    text-align: center;
  }

  .title {
    text-align: center;
  }
}

@media (max-width: 500px) {
  .aboutTitle {
    font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol";
    color: #696969;
    font-weight: 300;
    font-style: inherit;
    text-align: center;
    font-size: 1.5rem;
    padding-top: 5%;
    padding-bottom: 5%;
  }

  .aboutContainer img {
    display: none;
  }

  .aboutTextContainer p {
    overflow: hidden;
    text-align: justify;
    position: relative;
    padding-top: 25%;
    padding-bottom: 25%;
  }
}

@media (min-width: 501px) {
  .aboutTitle {
    font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol";
    color: #696969;
    font-weight: 300;
    font-style: inherit;
    text-align: center;
    font-size: 1.5rem;
    padding-top: 5%;
    padding-bottom: 5%;
  }

  .aboutTextContainer p {
    overflow: hidden;
    text-align: justify;
    position: relative;
    padding-top: 25%;
    padding-bottom: 25%;
  }
}

.aboutContainer {
  display: flex;
}
